/** Media Queries **/
/* CLIENT-SPECIFIC STYLES */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.outnone {
  outline: none;
  box-shadow: 0 0 0 0 transparent;
}

a,
button,
input,
textarea,
select {
  outline: none;
  box-shadow: 0 0 0 0 transparent;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    padding: 3rem 5rem;
    overflow: overlay;
  }
}

.login-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.barra {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton2 {
  color: #FFFFFF;
  font-weight: 700;
  margin: 0;
}
.barra .boton2:hover {
  color: #0da6f3;
}
.barra span {
  color: #0da6f3;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}
.barra-servicios .boton.active {
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #0da6f3;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 5rem 0;
  margin: 0 0 1rem 0;
}
@media (min-width: 768px) {
  .barra-servicios .boton.active {
    width: auto;
  }
}
.barra-servicios .boton.active:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  cursor: pointer;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listado-servicios .servicio p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}
.listado-servicios .servicio:hover {
  background-color: #e6e6e6;
  transform: scale(1.05);
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado:hover {
  background-color: #0a85c3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}

.contenedor-resumen span {
  color: #0da6f3;
  font-size: 2.4rem;
  font-weight: 700;
}
.contenedor-resumen p {
  margin-bottom: 1rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 0.1rem solid #e1e1e1;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 0.1rem solid #FFFFFF;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #0da6f3;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  color: #FFFFFF;
  font-weight: 400;
}

.no-hay {
  padding: 3rem 0;
  background-color: #b3b3b3;
  color: #FFFFFF;
  border-radius: 0.8rem;
}

.servicios .acciones {
  margin: 0;
}
.servicios .acciones a {
  color: #FFFFFF;
}
.servicios .acciones a:hover {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .servicios .boton,
  .servicios .boton-eliminar {
    margin: 0;
  }
}
.servicios .boton {
  margin-bottom: 1.5rem;
}
.servicios .boton-eliminar {
  border-radius: 1rem;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .campo {
    flex-direction: row;
  }
}
.campo label {
  flex: 0 0 3rem;
}
@media (min-width: 768px) {
  .campo label {
    flex: 0 0 15rem;
  }
}
.campo input {
  width: 100%;
  border: 1px solid #1a1b15;
  padding: 1.25rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .campo input {
    flex: 1;
  }
}
.campo input:focus {
  border: 1px solid #0da6f3;
  color: #0da6f3;
}
.campo input:disabled {
  background-color: #e6e6e6;
  color: #1a1b15;
  cursor: not-allowed;
}
.campo input:disabled::selection {
  display: none;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 5rem 0;
  margin: 0.5rem 0;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}

.acciones {
  margin-top: 0;
}
.acciones span {
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #0da6f3;
}
.acciones a:hover {
  color: #0a85c3;
}

.alerta {
  color: #FFFFFF;
  padding: 1.25rem;
  border-radius: 0.8rem;
  text-align: center;
  margin: 1rem 0;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
  cursor: default;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.error:hover {
  background-color: #980000;
}
.alerta.exito {
  align-items: center;
  background-color: #329f00;
}
.alerta span {
  cursor: pointer;
}
.alerta span::selection {
  display: none;
}
.alerta span:hover {
  color: #333333;
}

.alerta2 {
  display: block;
  color: #FFFFFF;
  padding: 1.25rem;
  border-radius: 0.8rem;
  text-align: center;
  margin: 1rem 0;
  font-weight: 700;
}
.alerta2.error {
  background-color: #cb0000;
}
.alerta2.error:hover {
  background-color: #980000;
}
.alerta2.exito {
  align-items: center;
  background-color: #329f00;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #0da6f3;
  border: none;
  display: block;
  width: 100%;
  padding: 1rem;
  margin-bottom: 1rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
}
.tabs button:hover {
  background-color: #0a85c3;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}
.tabs button:first-of-type {
  border-top-left-radius: 0.8rem;
  border-bottom-left-radius: 0.8rem;
}
.tabs button:last-of-type {
  border-top-right-radius: 0.8rem;
  border-bottom-right-radius: 0.8rem;
}

.paginacion {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}

.mostrar {
  visibility: visible;
}